* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;

  /* padding:  0 4rem; */
}

.header {
  background-color: rgb(50, 39, 39);
  display: flex;
  justify-content: center;
  padding: 5rem 0;
  background-color: #0d0828;
}
.header img {
  height: 10rem;
}

.LWM {
  display: flex;
  background-color: #2ae0f5;
  width: 90%;
  margin: auto;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 1rem;
  padding: 1rem;
  margin-top: -1.5rem;
  /* display: grid;
  grid-template-columns: repeat(3,1fr); */
}
.LWM img {
  height: 1.3rem;
}

.first_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  padding: 3rem 0;
}
.first_div #youtube {
  display: flex;
  width: 100%;
}
.second_div {
  display: flex;
  flex-direction: column;
  background-color: #cad2ff;
  padding: 2rem 1rem;
  gap: 1rem;
}
.about_me_imges {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}
.about_me_imges img {
  width: 100%;
}
/* .about_me_imges img {
  width: 16rem;
  height: 12rem;
} */
.third_div {
  display: flex;
  flex-direction: column;
  padding: 2rem 1rem;
  gap: 1rem;
  background-color: #b0ffec;
}
.third_div img {
  width: 100%;
}
.fourth_div img {
  width: 100%;
}
.work_imges {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}
/* .work_imges img {
  height: 12rem;
} */

.workk {
  height: 15rem;
}
.fourth_div {
  display: flex;
  flex-direction: column;
  padding: 2rem 1rem;
  gap: 1rem;
  background-color: antiquewhite;
}
.fourth_div img {
  width: 100%;
}
.music_imges {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}
/* .music_imges img {
  height: 12rem;
} */
.fifth_div {
  display: flex;
  flex-direction: column;
  background-color: #12074b;
  padding-top: 3rem;
  gap: 3rem;
  border-radius: 1rem;
}
.fifth_div img {
  align-items: center;
  justify-content: center;
  display: flex;
  /* align-self: center; */
  height: 5rem;
  margin: auto;
  margin-top: -5rem;
}
.logo1,
.logo2 {
  background-color: #291f5c;
  margin: 1rem;
  border-radius: 1rem;
  padding: 2rem;
  color: rgb(255, 255, 255);
}
.sixth_div {
  display: flex;
  flex-direction: column;
  padding: 2rem 2rem;
  gap: 3rem;
}
.sixth_div p {
  text-align: center;
}
.sixth_div img {
  background-color: #f83e7f;
  padding: 2rem;
  width: 14rem;
  border-radius: 50%;
  margin: auto;
}
.seventh_div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  background-color: rgb(195, 222, 243);
  padding: 2rem 1rem;
  gap: 1rem;
}
.seventh_div img {
  width: 100%;
}

.contact img {
  width: 2rem;
}
.contact {
  font-size: 0.7rem;
  gap: 2rem;
}
.email,
.phone {
  display: flex;
  gap: 1rem;
  padding: .5rem 0;
}

.find_me_here {
  float: right;
  width: 15rem;
}
